<template>
  <div class="home">
    <!--<div id="map" style="width: 100%; height: 800px;"></div>-->

    <div class="fixed-top">
      <nav class="navbar navbar-light bg-light">
        <a class="navbar-brand" href="/buy-mask">
          <img src="@/assets/logo.png" width="30" height="30" class="d-inline-block align-top rounded" alt="">
          口罩哪裡買
        </a>
        <div class="d-flex">
          <button class="navbar-toggler mr-1" type="button" data-toggle="collapse" data-target="#SearchContent" aria-controls="SearchContent" aria-expanded="false" aria-label="Toggle navigation">搜尋 <i class="fa fa-caret-up" aria-hidden="true"></i></button>
          <button v-if="!user.isLogined" class="navbar-toggler" type="button" @click="modal.login.show = true">登入</button>
          <button v-if="user.isLogined" class="navbar-toggler" type="button" data-toggle="collapse" data-target="#UserContent" aria-controls="UserContent" aria-expanded="false" aria-label="Toggle navigation">我的 <i class="fa fa-caret-up" aria-hidden="true"></i></button>
        </div>
      </nav>
      <div class="collapse" id="UserContent">
        <div class="bg-light">
          <ul class="list-group list-group-flush">
            <!--<li class="list-group-item text-right text-muted cursor-pointer" @click="getUserStores">我的訂閱藥局</li>-->
            <li class="list-group-item text-right text-muted cursor-pointer" @click="onLogout">登出</li>
          </ul>
        </div>
      </div>
      <div class="collapse" id="SearchContent">
        <div class="bg-dark py-4 px-2 px-sm-4 px-md-5">
          <div class="input-group mb-4">
            <div class="input-group-prepend">
              <button
                class="btn btn-light"
                type="button"
              >{{ (from.search.type === 'address') ? '地址' : '藥局名稱' }}</button>
              <span class="input-group-text cursor-pointer" @click="changeSearchType">
                <i class="fa fa-retweet" aria-hidden="true"></i>
              </span>
            </div>
            <input
              type="text"
              class="form-control"
              v-model="from.search.value"
              :placeholder="(from.search.type === 'address') ? 'ex: 高雄市前鎮區' : 'ex: 大樹藥局'"
              @keyup.enter="onSearch"
            />
            <div class="input-group-append">
              <button class="btn btn-light" type="button" @click="onSearch">找口罩</button>
            </div>
          </div>
          <div class="d-flex align-content-start" style="overflow-x: auto;">
            <rm-button-group :list="totalList" @oneClick="totalListClick"></rm-button-group>
            <rm-button-group :list="adultList" @oneClick="adultListClick"></rm-button-group>
            <rm-button-group :list="childList" @oneClick="childListClick"></rm-button-group>
          </div>
          <div>
            <span class="badge badge-primary px-2 py-1 mr-2 mb-1">#充足 (數量 &gt; 50)</span>
            <span class="badge badge-primary px-2 py-1 mr-2 mb-1">#有限 (數量 &gt; 20)</span>
            <span class="badge badge-primary px-2 py-1 mr-2 mb-1">#不足 (數量 &lt; 20)</span>
          </div>
        </div>
      </div>
    </div>

    <div class="container">
      <div v-if="isLoading" class="row mt-5">
        <div class="col-12">
          <div class="spinner-border text-info" role="status">
            <span class="sr-only">Loading...</span>
          </div>
        </div>
      </div>
      <div v-else>
        <div v-if="stores.length > 0" class="row mt-4">
          <div class="col-md-6" v-for="store in stores" :key="store.code">
            <div class="store-card mb-4">
              <div class="store-card-body">
                <div class="d-flex justify-content-between align-items-center mb-3">
                  <span class="badge px-2 py-1" :class="getMaskTotalLevel(store.adult_count + store.child_count)">{{ getMaskTotalLevelText(store.adult_count + store.child_count) }}</span>
                  <div class="update-time text-muted">{{ getUpdateTime(store.updated_time) }}更新</div>
                </div>
                <h4 class="card-title">
                  {{ store.name }}
                  <small class="card-sub-title">{{ getBusinessHour(store.business_hours) }}</small>
                </h4>
                <p class="store-address mb-4">
                  <span class="mr-1">{{ store.address }}</span> <span v-if="store.distance">(<i class="fa fa-location-arrow" aria-hidden="true"></i> {{ store.distance }} 公尺)</span>
                </p>

                <div class="mask-stock adult mb-2">
                  <div class="label">大人</div>
                  <div class="value">{{ store.adult_count }}</div>
                </div>
                <div class="mask-stock child">
                  <div class="label">小孩</div>
                  <div class="value">{{ store.child_count }}</div>
                </div>
              </div>
              <!--<div class="store-card-footer" :class="{ logined: user.isLogined }">-->
              <div class="store-card-footer">
                <a class="link" :href="getPhone(store.phone)">
                  <i class="fa fa-phone"></i>
                  撥打電話
                </a>
                <a class="link" target="_blank" :href="getGoogleMap(store.lat, store.lng)">
                  <i class="fa fa-paper-plane"></i>
                  導航前往
                </a>
                <!--<a v-if="user.isLogined" class="link" @click="orderStore(store.id)">
                  <i class="fa fa-bell-slash"></i>
                  訂閱通知
                </a>-->
              </div>
            </div>
          </div>
        </div>
        <div v-else class="col-md-12 text-center" style="margin-top: 40vh;">
          <h3>找不到符合的資料...</h3>
        </div>

        <modal v-if="modal.login.show" :hideHeader="true" :hideFooter="true" :width="'400px'">
          <div slot="body" class="user-login-modal-body">
            <div class="form-row">
              <div class="form-group col-12">
                <input type="text" class="form-control" placeholder="手機號碼" v-model="from.user.phone">
              </div>
              <div class="form-group col-12">
                <select class="form-control" v-model="from.user.odd_or_even">
                  <option value="0">身分證最後一碼</option>
                  <option value="1">單數</option>
                  <option value="2">偶數</option>
                </select>
              </div>
              <div class="col-6">
                <rm-button text="登入" type="success" size="medium" :isFill="true" @click="onLogin"></rm-button>
              </div>
              <div class="col-6">
                <rm-button text="取消" type="danger" size="medium" :isFill="true" @click="modal.login.show = false"></rm-button>
              </div>
            </div>
          </div>
        </modal>

      </div>
    </div>
  </div>
</template>

<script>
// import mapboxgl from "mapbox-gl/dist/mapbox-gl.js";
import storage from '@/utils/storage'

const Modal = () => import("@/components/Modal");
const RmButton = () => import("@/components/RmButton");
const RmButtonGroup = () => import("@/components/RmButtonGroup");

export default {
  name: "home",
  components: {
    RmButtonGroup,
    Modal,
    RmButton
  },
  data() {
    return {
      isLoading: false,
      isSearch: false,
      coords: {
        lat: 0,
        lng: 0
      },
      map: null,
      from: {
        search: {
          type: "address",
          value: ""
        },
        filter: {
          totalList: '',
          adultList: '',
          childList: ''
        },
        user: {
          phone: '',
          odd_or_even: 0
        }
      },
      modal: {
        login: {
          show: false
        }
      },
      totalList: [
        {
          name: 'suff',
          type: 'success',
          size: 'small',
          text: '總量庫存充足',
          round: true,
          showClickedStyle: false
        },
        {
          name: 'limit',
          type: 'warning',
          size: 'small',
          text: '總量庫存有限',
          round: true,
          showClickedStyle: false
        },
        {
          name: 'no_suff',
          type: 'danger',
          size: 'small',
          text: '總量庫存不足',
          round: true,
          showClickedStyle: false
        }
      ],
      adultList: [
        {
          name: 'suff',
          type: 'success',
          size: 'small',
          text: '成人庫存充足',
          round: true,
          showClickedStyle: false
        },
        {
          name: 'limit',
          type: 'warning',
          size: 'small',
          text: '成人庫存有限',
          round: true,
          showClickedStyle: false
        },
        {
          name: 'no_suff',
          type: 'danger',
          size: 'small',
          text: '成人庫存不足',
          round: true,
          showClickedStyle: false
        }
      ],
      childList: [
        {
          name: 'suff',
          type: 'success',
          size: 'small',
          text: '小孩庫存充足',
          round: true,
          showClickedStyle: false
        },
        {
          name: 'limit',
          type: 'warning',
          size: 'small',
          text: '小孩庫存有限',
          round: true,
          showClickedStyle: false
        },
        {
          name: 'no_suff',
          type: 'danger',
          size: 'small',
          text: '小孩庫存不足',
          round: true,
          showClickedStyle: false
        }
      ],
      orgStores: [],
      stores: [],
      weekday: moment().format('e'),
      dayHour: parseInt(moment().format('HH'))
    };
  },
  computed: {
    user() {
      return this.$store.state.user;
    }
  },
  mounted() {
    // this.mapInit();
    this.searchLocation();
  },
  methods: {
    async searchLocation() {
      const hasLoc = await this.getLocation();
      if (!hasLoc) return;

      this.isLoading = true;

      const res = await this.$api.location(this.coords.lng, this.coords.lat);

      this.orgStores = res.data;
      this.stores = res.data;

      this.initFilter();

      this.isLoading = false;
    },
    async onSearch() {
      if (this.isSearch || !this.from.search.value) return;

      this.isLoading = true;
      this.isSearch = true;

      const res = await this.$api.search(
        this.from.search.type,
        this.from.search.value,
        this.coords.lng,
        this.coords.lat
      );

      this.orgStores = res.data;
      this.stores = res.data;

      this.initFilter();

      this.isLoading = false;
      this.isSearch = false;
    },
    async getUserStores() {
      $('#UserContent').collapse('toggle');

      this.isLoading = true;

      const res = await this.$api.userStores(this.coords.lng, this.coords.lat);

      this.orgStores = res.data;
      this.stores = res.data;

      this.initFilter();

      this.isLoading = false;
    },
    async orderStore(id) {
      const res = await this.$api.orderStore(id);

      if (res.code === '00000') alert('已訂閱');
    },
    async deleteStore(id) {
      const res = await this.$api.deleteStore(id);

      if (res.code === '00000') alert('已取消訂閱');
    },
    getLocation() {
      return new Promise((resolve, reject) => {
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(p => {
            this.coords.lng = this.roundDecimal(p.coords.longitude, 4);
            this.coords.lat = this.roundDecimal(p.coords.latitude, 4);
            resolve(true);
          });
        } else {
          reject(false);
        }
      });
    },
    roundDecimal(val, precision) {
      return Math.round(Math.round(val * Math.pow(10, (precision || 0) + 1)) / 10) / Math.pow(10, (precision || 0));
    },
    /*async mapInit() {
      const hasLoc = await this.getLocation();
      if (!hasLoc) return;

      mapboxgl.accessToken =
        "pk.eyJ1Ijoicm95YWxtYXIiLCJhIjoiY2p2NTV2N3YwMnkzdDN5cGZuMjgzNm56ayJ9.nFhZNElKWpm8zkQ-9mQM0Q";

      this.map = new mapboxgl.Map({
        container: "map",
        style: "mapbox://styles/mapbox/streets-v9?optimize=true",
        center: [this.coords.lng, this.coords.lat],
        minZoom: 6,
        maxZoom: 18,
        zoom: 13
      });

      this.setMarker(this.coords, { color: "#e91e63" });
    },
    setMarker(coords, options = {}, popup = null) {
      new mapboxgl.Marker(options).setLngLat(coords).addTo(this.map);
    },*/
    changeSearchType() {
      this.from.search.type = (this.from.search.type === 'address') ? 'name' : 'address';
    },
    totalListClick(index, clickedIndex) {
      this.listClick('totalList', index, clickedIndex);
    },
    adultListClick(index, clickedIndex) {
      this.listClick('adultList', index, clickedIndex);
    },
    childListClick(index, clickedIndex) {
      this.listClick('childList', index, clickedIndex);
    },
    listClick(name, index, clickedIndex) {
      this[name].forEach(el => {
        el.showClickedStyle = false;
      });
      
      if (index === clickedIndex) {
        this.from.filter[name] = '';
      }
      else {
        this[name][index].showClickedStyle = true;
        this.from.filter[name] = this[name][index].name;
      }

      this.onFilter();
    },
    initFilter() {
      this.listClick('totalList', null, null);
      this.listClick('adultList', null, null);
      this.listClick('childList', null, null);

      this.onFilter();
    },
    onFilter() {
      this.stores = this.orgStores;

      if (this.from.filter.totalList) {
        if (this.from.filter.totalList === "suff") {
          this.stores = this.stores.filter(store => {
            let count = store.adult_count + store.child_count;
            return count >= 50;
          });
        } else if (this.from.filter.totalList === "limit") {
          this.stores = this.stores.filter(store => {
            let count = store.adult_count + store.child_count;
            return count >= 20 && count < 50;
          });
        } else if (this.from.filter.totalList === "no_suff") {
          this.stores = this.stores.filter(store => {
            let count = store.adult_count + store.child_count;
            return count < 20;
          });
        }
      }

      if (this.from.filter.adultList) {
        if (this.from.filter.adultList === "suff") {
        this.stores = this.stores.filter(store => {
          return store.adult_count >= 50;
        });
      } else if (this.from.filter.adultList === "limit") {
        this.stores = this.stores.filter(store => {
          return store.adult_count >= 20 && store.adult_count < 50;
        });
      } else if (this.from.filter.adultList === "no_suff") {
        this.stores = this.stores.filter(store => {
          return store.adult_count < 20;
        });
      }
      }

      if (this.from.filter.childList) {
        if (this.from.filter.childList === "suff") {
        this.stores = this.stores.filter(store => {
          return store.child_count >= 50;
        });
      } else if (this.from.filter.childList === "limit") {
        this.stores = this.stores.filter(store => {
          return store.child_count >= 20 && store.child_count < 50;
        });
      } else if (this.from.filter.childList === "no_suff") {
        this.stores = this.stores.filter(store => {
          return store.child_count < 20;
        });
      }
      }

      this.storeSort();
    },
    storeSort() {
      if (!this.coords.lng || !this.coords.lat) return;

      this.stores.sort((a, b) => {
        return a.distance - b.distance;
      });
    },
    getMaskTotalLevel(count) {
      if (count >= 50) return "badge-success";
      else if (count >= 20 && count < 50) return "badge-warning";
      else return "badge-danger";
    },
    getMaskTotalLevelText(count) {
      if (count >= 50) return "# 庫存充足";
      else if (count >= 20 && count < 50) return "# 庫存有限";
      else return "# 庫存不足";
    },
    getBusinessHour(businessHours) {
      if (!businessHours) return '';

      businessHours = businessHours.filter(b => b.indexOf(this.getWeekday()) !== -1);
      if (!businessHours) return '';

      let ft = '上午';
      if (this.dayHour >= 12 && this.dayHour < 17) {
        ft = '下午';
      }
      else if (this.dayHour >= 17 && this.dayHour < 24) {
        ft = '晚上';
      }

      businessHours = businessHours.filter(b => b.indexOf(ft) !== -1);
      if (!businessHours) return '';
      return businessHours[0];
    },
    getWeekday() {
      const weekdays = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五' , '星期六'];
      return weekdays[this.weekday];
    },
    getPhone(phone) {
      phone = phone.replace('(', '');
      phone = phone.replace(')', '');
      phone = phone.replace('-', '');

      return 'tel:' + phone;
    },
    getGoogleMap(lat, lng) {
      if (!this.coords.lng || !this.coords.lat) {
        return `https://www.google.com.tw/maps/dir/${lat},${lng}`;
      } else {
        return `https://www.google.com.tw/maps/dir/${this.coords.lat},${this.coords.lng}/${lat},${lng}`;
      }
    },
    getUpdateTime(createtime) {
      let now = moment(new Date()).format("X");
      let updateTime = moment(createtime).format("X");

      var limit = now - updateTime;
      var content = "";
      if (limit < 60) {
        content = limit + " 秒前";
      } else if (limit >= 60 && limit < 3600) {
        content = Math.floor(limit / 60) + " 分鐘前";
      } else {
        content = Math.floor(limit / 3600) + " 小時前";
      }

      return content;
    },
    async onLogin() {
      if (!this.from.user.phone || !this.from.user.odd_or_even) return alert('請確認欄位是否填寫');
      if (!this.checkMobile(this.from.user.phone)) return alert('請確認手機格式');

      const res = await this.$api.login(this.from.user);

      if (res.code = '00000') {
        let user = {
          isLogined: true,
          id: res.data.id,
          phone: res.data.phone,
          token: res.data.token
        };

        this.$store.dispatch('actionSetUser', user)
        storage.set('user', user);
        this.$api.setToken(user.token);

        // get FCM token
        this.$messaging.requestPermission()
        .then(res => {
          // 若允許通知 -> 向 firebase 拿 token
          return this.$messaging.getToken();
        })
        .then(token => {
          // 成功取得 token
          this.$api.updateFCMToken(token);
        });

        this.modal.login.show = false;
      }
      else {
        alert('error');
      }
    },
    onLogout() {
      $('#UserContent').collapse('toggle');

      storage.remove('user');
      this.$store.dispatch('actionSetUserInit');
    },
    checkMobile(str) {
      var result = false;
      if(str.length > 0){
        //是否只有數字;
        var patt_mobile = /^[\d]{1,}$/;
        result = patt_mobile.test(str);
    
        if(result){
          //檢查前兩個字是否為 09
          //檢查前四個字是否為 8869
          var firstTwo = str.substr(0,2);
          var firstFour = str.substr(0,4);
          var afterFour = str.substr(4,str.length-1);
          if(firstFour == '8869'){
            if(afterFour.length == 8){
              result = true;
            } else {
              result = false;
            }
          } else if(firstTwo == '09'){
            if(str.length == 10){
              result = true;
            } else {
              result = false;
            }
          } else {
            result = false;
          }
        }
      } else {
        result = true;
      }
      return result;
    }
  }
};
</script>

